.movie {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 50px;
    padding-top: 70px;
    width: 80%;
}

.movie .movie {
    width: 45%;
    background-color: white;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    padding : 20px;
    border-radius: 5px;
    color: #adaeb9;
    box-shadow: 0 13px 270x -5px rgba(50 50, 93 0.25),
    0 8px 16px -8px rgb(0,0,0,0.3), 
    0 -6px 16px -6px rgba(0,0,0,0.025);
}

.movie img {
    position: relative;
    top: -50px;
    max-width: 150px;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50,50,93,0.25),
    0 8px 16px -8px rgba(0,0,0,0.3),
    0 -6px 16px -6px rgba(0,0,0,0.025);
}

.movie .movie_title,
.movie .movie_year {
    margin: 0;
    font-weight: 300;
}

.movie .movie_title {
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;;
}

.movie .movie_genres {
    list-style: none;
    padding: 0px;
    margin: 0px;
    display: flex;
    margin: 5px 0px;
}

.movie_genres li,
.movie .movie_year{
    margin-right: 10px;
    font-size: 14px;
}